import React, { useMemo } from "react";
import Layout from '../../../layout';
import { Grid, Button } from "platyplex_ui";
import Header from "./header";
import { useSelector } from "react-redux";
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import { defaultInterval } from '../../../utils/time';
import { Modal } from "platyplex_ui";
import { Editor } from "./editor";
import { TimeSeries } from '../../../model/domain/metrics';
import {  useSelectEnterpriseEvent } from '../model';
import chart from './chart.json';

const ts = (timeSeriesQuery: TimeSeries.PartialQuery | any) => ({
    ...timeSeriesQuery,
    interval: defaultInterval()
})

const Analytics = ({ switches }: { switches: JSX.Element }) => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { enterpriseEvent: activeEvent } = useSelectEnterpriseEvent();

    const { revenueAndTransactions: chart1, reservations: chart2 } = activeEvent.getLiveCharts();

    const ms11 = useMetricData({
      timeseries: ts(chart1.revenueTimeSeriesQuery),
      eId
    });
    
    const ms12 = useMetricData({
      timeseries: ts(chart1.transactionTimeSeriesQuery),
      eId
    });

    const ms21 = useMetricData({
      timeseries: ts(chart[1].timeSeriesQuery),
      eId
    });

  useMemo(() => {
    ms11.refresh(chart1.revenueTimeSeriesQuery);
    ms12.refresh(chart1.transactionTimeSeriesQuery);
    ms21.refresh(chart[1].timeSeriesQuery)
  }, [activeEvent.id]);
 
  const EditModal = Modal({
    title: "Dynamic Pricing",
    message: <Editor add={()=>{}} close={() => EditModal.close()} />,
    hideCancel: true,
  });

    return (
        <Layout sidebar>
            <Layout.Top >
                {switches}
                <Header />
            </Layout.Top>
            <Layout.Bottom>
                <Grid placeItems="start stretch" padding="1em" height="100%">
                    <Grid cols="2" gridGap="0 1em">
                        <Button
                            placeSelf="end"
                            rounded
                            invert
                            shadow
                            onClick={EditModal.open}
                            style={{ gridColumn: '1/-1' }}
                            faIcon={`fa fa-edit`}
                        > Dynamic Pricings</Button>
                            <Chart.Multi
                                className='slide-up'
                                inChartLegend
                                fixedAlignment={'5m'}
                                metricDataStatuses={[ms11.metricDataStatus, ms12.metricDataStatus]}
                                eId={eId}
                                download={'event-revenue-transaction'}
                                queries={{revenueTimeSeriesQuery : ms11.timeseries, transactionTimeSeriesQuery: ms12.timeseries}}
                                {...chart1 as any} 
                            />

                            <Chart
                                inChartLegend
                                isPercent={false}
                                className='slide-up'
                                metricDataStatus={ms21.metricDataStatus}
                                {...chart2 as any}
                                eId={eId} 
                                queries={{ timeSeriesQuery : ms21.timeseries}}
                                download='garage-occupancy'
                            />
                    </Grid>
                </Grid>
            </Layout.Bottom>
        </Layout>
    );
};

export default Analytics;