import { ActionTypes } from '../reducer/user';
import api from '../../api';
import { Notification } from 'platyplex_ui';
import { User } from '../../model/domain/user';
import { overflow } from '../../utils/text';
import logger from '../../utils/logger';
import Key from "../../json/keys.json";
import { Dispatch } from 'react';

export const asyncActions = async (apiCall: () => Promise<any>, dispatch: any, showError = false) => {
    try {
        dispatch({ type: ActionTypes.USER_REQUEST_PENDING });
        let payload = await apiCall();
        if (!payload || !payload.success)
            throw new Error(payload.message || "Error");

        dispatch({ type: ActionTypes.USER_REQUEST_SUCCESS })
        return await payload;
    } catch (e) {
        logger.log(e)
        if (showError) {
            Notification.error({ title: "Error", message: String(e?.message || e) });
        }
        dispatch({ type: ActionTypes.USER_REQUEST_FAILED })
    }
}

const formatUser = (user: User) => ({ ...user, name: overflow(user.name) });
const isLoggedIn = () =>  Boolean(localStorage.getItem(Key.USER));

export const auth = () => {
    return async (dispatch: any) => {
        if (isLoggedIn()) {
            const payload = (await asyncActions(() => api.auth(), dispatch));
            if (payload && payload.success) {
                console.log(payload)
                await getEnterprise(payload.user, dispatch)
                dispatch({ type: ActionTypes.AUTH, user: formatUser(payload.user), token: payload.authToken });           
            }
        }
    }
}

const getEnterprise = async (user: User, dispatch: any) => {
    if (user?.apps?.enterpriseApp === 'enabled') {
        const { id } = user;

        const enterprisePayload = (await asyncActions(() => api.getEnterpriseByUserId(id), dispatch));
        console.log('HERE')
        console.log(enterprisePayload)
        if (enterprisePayload?.success)
            await dispatch({ type: ActionTypes.GET_ENTERPRISE, enterprise: enterprisePayload.enterprise });
    }
}

export const login = (props: any) => {
    return async (dispatch: any) => {
        const payload = await asyncActions(() => api.login(props), dispatch, true);
        if (payload && payload.success) {
           localStorage.setItem(Key.USER, payload.authToken)           
           dispatch(auth())
        }
    }
}

export const signup = (props: any) => {
    return async (dispatch: Dispatch<any>) => {
        const payload = await asyncActions(async () => await api.signup(props), dispatch, true);

        if (payload && payload.success) {
            await getEnterprise(payload.user, dispatch)
            dispatch({ type: ActionTypes.SIGNUP, user: formatUser(payload.user), token: payload.authToken })           
        }
    }
}

export const addEnterprise = (enterpriseId: string, userId: string) => {
    return async (dispatch: any) => {
        const payload = (await asyncActions(() => api.selectEnterpise(enterpriseId, userId), dispatch, true));
        if (payload && payload.success) {
            dispatch({ type: ActionTypes.ADD_ENTERPISE })
        }
    }
}

export const logout = () => (dispatch: Dispatch<{type: ActionTypes}>) => dispatch({ type: ActionTypes.LOGOUT });