import React, { useState } from "react";
import { Grid, Switch } from "platyplex_ui";
import { useSelector } from "react-redux";
import Table from './table';
import { Abnormality as AB } from "./type";

const states = {
    ACTIVE: "ACTIVE",
    ALL: "ALL",
    PAST: "PAST",
    DEFAULT: "ACTIVE",
    states: ["ACTIVE", "PAST", "ALL"],
};

const Abnormality = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const [state, setState] = useState({ active: states.DEFAULT });
    
    const abnormalities: AB[] = [{
        id: '1',
        garage: 'North 10th St Garage',
        detected_on: '09/09/2020 @ 10:00 AM',
        active: true,
        abnormality_event: 'Occupancy detected to be 15% above than last three weeks moving average <br/>Occupancy detected: <span style="color:red">98% </span><br/>Last 3 weeks moving average: <span style="color:green">83%</span>'
    },
    {
        id: '2',
        garage: 'South 7th St Garage',
        detected_on: '09/04/2020 2:00 PM',
        active: true,
        abnormality_event: 'Occupancy detected to be 15% below than last year data <br/>Occupancy detected: <span style="color:red">72% </span><br/>Last year data as of Mon, Sep 2, 2020 at 2:00 PM: <span style="color:green">87%</span>'
    },
    {
        id: '3',
        garage: 'South 7th St Garage',
        detected_on: '8/20/2020 1:00 PM',
        active: true,
        abnormality_event: 'Occupancy detected to be 8% above than custom set date <br/>Occupancy detected: <span style="color:red">99% </span><br/>Occupancy Data as of Tue, Oct 13, 2020 at 1:00 PM: <span style="color:green">91%</span>'
    }];


    return (
        <Grid placeItems="start stretch" padding="1em" height="100%">
            <Grid>
                <Table
                    abnormalities={abnormalities.filter((ab: AB) => state.active === states.ALL || (ab.active && state.active === states.ACTIVE) || ((!ab.active && state.active === states.PAST)))}
                    toggler={
                        <Switch
                            style={{ margin: "0 0 .5em 0" }}
                            active={state.active}
                            items={states.states}
                            onClick={(active: string) => setState({ ...state, active })}
                        />
                    }
                />
            </Grid>
        </Grid>
    );
};

export default Abnormality;