import React, { useState } from 'react';
import { Switch } from "platyplex_ui";
import Layout from '../../layout';
import Listings from './listings';
import Analytics from './analytics';
import OccupancyTable from './occupancy-table';
import Header from "./header";
import Abnormality from './abnormality';
import { Badge } from 'antd';

enum activePages {
  "ANALYTICS" = "ANALYTICS",
  "LISTINGS" = "LISTINGS",
  "OCCUPANCY_TABLE" = "OCCUPANCY_TABLE",
  "ABNORMALITY" = "ABNORMALITY"
};

const Garage = () => {
  const [activePage, setActivePage] = useState(activePages.ANALYTICS);
  const items = [
    ...Object.keys(activePages).splice(0, 3),
    {
      toString: () => 'ABNORMALITY',
      el: <Badge  count={3}><span style={{fontSize: '1.25em'}}>Abnormality &nbsp; &nbsp;</span></Badge>
    }
  ]
  return (
    <Layout sidebar>
      <Layout.Top>
        <Switch.underlined
          style={{ placeSelf: "start", gridColumn: '1/-1', gridGap: '.5em 1.5em' }}
          items={items}
          onClick={(active: activePages) => setActivePage(active)}
          active={activePage}
        />
        <Header />
      </Layout.Top>
      <Layout.Bottom>
        {activePage === activePages.LISTINGS ? <Listings /> :
          activePage === activePages.OCCUPANCY_TABLE ? <OccupancyTable /> :
            activePage === activePages.ABNORMALITY ? <Abnormality /> :
              <Analytics />
        }
      </Layout.Bottom>
    </Layout>
  );
}

export default Garage;