import React from 'react';
import { Grid } from "platyplex_ui";
import useGarages from '../../../hooks/garage';
import { Garage } from '../../../model/domain/garage';
import GarageInfoForm from './garage-form';

export const GarageEditor = ({ close, eId, garage, refresh, tables }: any) => {
    const { editGarage, loading } = useGarages({ eId });
    const submit = async (garage: Garage) => {
        await editGarage(garage);
        close();
        refresh();
    }

    return (
        <Grid width="100%" height="100%" gridGap="0">
            <GarageInfoForm submit={submit} tables={tables} garage={garage} loading={loading} />
        </Grid>);
}