import React from 'react';
import { Grid, Header as Text } from "platyplex_ui";
import useGarages from '../../../hooks/garage';
import { useSelector } from 'react-redux';
import { ScoreCard } from '../../../components/score-card/score-card';
import { moment } from '../../../utils/time';
import { invertedTxtColor } from '../../../config';

const Header = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);

    return (
        <Grid
            cols='5'
            style={{ gridColumn: '1/-1' }}
            width='100%'
            padding='1.5em 1.5em 0 1.5em'
        >  
            <Text
                className='hide-mobile'
                style={{ gridColumn: '1/-1', placeSelf: "end" }}
                size={4.6}
                color={invertedTxtColor}
            >
                Updated at: {moment().format('hh:mm A')}
            </Text>

            <ScoreCard title='Active Events' faIcon='fa fa-event' hoverLabel='Active Events' borderRight>
                <span className='txt-lgx'>2</span>
                <span className='txt-md'>/2</span>
            </ScoreCard>

            <ScoreCard title='Past Events' faIcon='fa fa-event' hoverLabel='Past Events' borderRight>
                <span className='txt-lgx'>0</span>
            </ScoreCard>

        </Grid>);
}

export default Header;