import React from "react";
import { Card, Empty, Grid} from "platyplex_ui";
import { Table } from "antd";
import { primaryColor, mobileWidth } from "../../../config";
import styled from "styled-components";
import columns from "./columns";

let locale = {
  emptyText: <Empty text="No Abnormality" />
};

export default ({ abnormalities, toggler }: any) => {
  const activate = (i : any)=>{}
  return (
    <Card shadow minWidth="60vw" padding=".5em" width='100%'>
      <Grid customCols="auto 1fr auto">
        <div></div>
        {toggler}
      </Grid>
      <DIV>
        <Table
          className='min-height-table'
          locale={locale}
          key={Math.random() * 1000}
          loading={false}
          style={{ color: primaryColor}}
          dataSource={abnormalities}
          columns={columns(
            (i: any) => activate(i), 
            false
          )}
        />
      </DIV>
    </Card>
  );
};

const DIV = styled.div`
  @media (max-width: ${mobileWidth}) {
    overflow-x: scroll;
  }
`;