import { Space, Switch } from 'antd';
import { Text} from "platyplex_ui";
import React from 'react';
import { Popconfirm } from 'antd';
import { moment, FORMAT_DATETIME } from '../../../utils/time';

const columns = (activate: any, loadingAction = false, index = 0) => {
    return [{
        title: 'Garage',
        dataIndex: 'garage',
        key: 'garage',
        render: (text: string) => <div className='txt-overflow'>{text}</div>
    },
    {
        title: 'Detected On',
        dataIndex: 'detected_on',
        key: 'detected_on',
        render: (t: string) => moment(t).format(FORMAT_DATETIME)
    },
    {
        title: 'Abnormality Event',
        dataIndex: 'abnormality_event',
        key: 'abnormality_event',
        render: (text: string) => <span dangerouslySetInnerHTML={{__html: text}}/>
    },
    {
        title: 'Action',
        key: 'action',
        render: (_: any, record: any) => {
            return (
                <Space size="middle">
                    <Popconfirm
                        okButtonProps={{ size: 'middle' }}     
                        cancelButtonProps={{ size: 'middle' }}
                        okText='Yes, switch back'
                        cancelText='No, I need more time to check'
                        title={
                        <div style={{maxWidth: '400px'}}>
                            <b>Are you sure you want to switch back to showing current Occupancy Values?</b>
                            <br/> Please make sure to check if your hardware including any camera/sensors are working properly and all your servers are online before switching back.
                        </div>}
                        onConfirm={() => activate(record)}
                    >
                        <Text>
                            <Switch
                                disabled={loadingAction}
                                defaultChecked
                            />
                        </Text>
                    </Popconfirm>
                </Space>
            )
        },
    },
    ]
};


export default columns;