import React, { useEffect, useState } from "react";
import { Header, Grid, Notification } from 'platyplex_ui';
import { primaryColor } from "../../../config";
import { overflow } from "../../../utils/text";
import { Charts } from "../types";
import charts from '../json/charts.json'
import api from "../../../api";
import logger from "../../../utils/logger";
import { Button } from "../../../style";

const ChartHeader = ({
  chartType,
  chartTypes,
  setChartType,
  title, filter,
  isPercent,
  loading = false,
  download,
  eId,
  queries
}: any) => {
  const [link, setLink] = useState('')


  useEffect(() => {
    if (eId && download && queries) {
      const ts = Object.keys(queries)
        .reduce((acc, key, i) => acc + `${i > 0 ? '&' : ''}${key}=${JSON.stringify(queries[key])}`, '')
      api.downloadCSv(eId, download, ts)
        .then((res: any) => {
          if (res.success) {
            setLink(res.csvExportUrl)
          }
        })
        .catch((e) => logger.log(e))
    }
  }, [eId, queries])

  return (
    <Header
      className="grid"
      color={primaryColor}
      style={{
        gridColumn: "1/-1",
        width: "100%",
        gridTemplateColumns: "1fr auto auto",
        placeItems: "start ",
      }}
      bold
      size={4}
    >
      <Grid mobileLayout={false} customCols="auto 1fr auto" gridGap=".5em" padding=".2em .5em"
        width={'100%'}
        style={{
          placeItems: 'start',
          borderRadius: "1em",
          textTransform: "capitalize",
          placeSelf: "start",
        }}
      >
        <Header bold size={3.75} textAlign='left'>{overflow(title, 25)}</Header>
        <div style={{ placeSelf: "end" }}>
          {download && <Button
            invert
            minWidth="3em"
            margin='0 1em'
            noBorder={true}
            onClick={() => {
              window.open(link)
              Notification.success({ title: 'Success', message: 'Exporting Data to Excel' })
            }}
            faIcon={'fas fa-file-download'}
            tooltip='Download the current data on Excel'
          />}

          {chartTypes?.length > 1 &&
            charts.map(({ key, faIcon, label }) => (
              chartTypes && <span key={key}>{chartTypes.includes(key) &&
                <Button
                  invert={chartType !== key}
                  minWidth="3em"
                  disabled={loading || (isPercent && key !== Charts.multiLine)}
                  noBorder={chartType !== key}
                  onClick={() => setChartType(key as Charts)}
                  faIcon={faIcon}
                  tooltip={label}
                />}
              </span>
            ))
          }
        </div>
        {filter}
      </Grid>
    </Header>
  )
};
export default ChartHeader;